* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppies", sans-serif;
}

:root {
  --bgcolor: #f1f5fd;
  --primary-color: #6f5de7;
  --text-color: #828286;
  --main-text-color: #333333;
}

html {
  font-size: 62.5%;
}

a {
  text-decoration: none;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333333;

}



/* ------------------------------navbar css style--------------------------------*/
.navbar-bg {
  background-color: var(--bgcolor);
  padding: 2rem 0;
  z-index: 99;
  position: relative;
}

.navbar-brand {
  font-size: 2.5rem;

}

.navbar li {
  padding: 0 2rem;
}


.navbar li a {
  font-size: 1.7rem;
  font-weight: lighter;
  position: relative;
}

.navbar li a:hover {
  color: var(--primary-color) !important;
  text-shadow: 0 0.2rem 0.3rem rgba(183, 171, 255, 0.5);
}

.btn-style {
  width: 12rem;
  height: 4rem;
  border-radius: 2rem;
  color: #fff;
  font-size: 1.6rem;
  background-color: var(--primary-color);
  text-transform: capitalize;
}

.btn-style:first-child {
  margin-right: 2rem;
}

.btn-style:hover {
  border: 1px solid var(--primary-color);
  background-color: transparent;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);

}

.btn-border {
  border: 1px solid var(--primary-color);
  background-color: transparent;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);
}

.btn-border:hover {
  border-radius: 2rem;
  color: #fff;
  background-color: var(--primary-color);
}


/* ---------------- navbar end-------------------*/


/* ---------------- Hero section start -------------------*/


header {
  background-color: var(--bgcolor);
  min-height: 100vh;
  width: 100%;
}

.main-hero-container {
  height: 80vh;
  display: flex;
  /* background-color: aqua; */
}

.main-hero-container h1 {
  text-transform: capitalize;
  font-weight: bolder;
}

.main-hero-para {
  font-size: 1.5rem;
  color: var(--text-color);
  margin: 3rem 0 5rem 0;
  font-weight: lighter;
  width: 75%;
}

.main-hero-container h3 {
  font-weight: lighter;
}

.input-group {
  position: relative !important;
}

.form-control-text {
  position: absolute;
  height: 5rem;
  border: none;
  outline-style: none;
  padding-left: 1.5rem !important;
  font-size: 1.4rem;
  font-weight: lighter;
}

.input-group-button {
  position: absolute;
  left: 59%;
  top: 0.5rem;
  color: #fff;
  background-color: var(--primary-color);
  height: 4rem;
  width: 15%;
  border-radius: 2rem !important;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  cursor: pointer;
}

.input-group-button:hover {
  border: 1px solid var(--primary-color);
  background-color: transparent;
  color: var(--primary-color);
  box-shadow: 0 0.2rem 0.5rem rgba(183, 171, 255, 0.5);

}

/* right side of hero section  */

.main-herosection-images {
  position: relative;
}

.main-herosection-images img {
  width: 60%;
  height: 75%;
  margin-top: 10rem;
  box-shadow: 0 0.8rem 0.8rem rgba(0, 0, 0, 0.25);
  border-radius: 1.5rem;
  position: absolute;
}

.main-herosection-images .main-hero-img2 {
  position: absolute;
  width: 50%;
  height: 30%;
  left: 55%;
  top: -2%;
  box-shadow: 0.8rem 0 0.8rem rgba(0, 0, 0, 0.25);
}


/* ---------------- Hero section end -------------------*/
/* ---------------- work section start -------------------*/

.work-container {
  width: 90vw;
  padding: 5rem;
  box-shadow: 0 0.4rem 1.2rem rgba(0, 0, 0, 0.1);
  margin-top: -10rem;
  background-color: #fff;
  border-radius: 0.5rem;
}

.main-heading {
  font-size: 3rem;
  font-weight: bolder;
  margin-bottom: 2rem;
  text-transform: capitalize;
  word-spacing: 1rem;
  text-shadow: 0 0.2rem 0.3rem rgba(183, 171, 255, 0.5);
}


.work-container-subdiv {
  padding: 0 6rem;
}

.sub-heading {
  font-size: 2rem;
  font-weight: normal;
  /* color: rgb(210, 49, 49); */
}

.fontawesome-style {
  font-size: 2.3rem;
  color: var(--primary-color);
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background-color: var(--bgcolor);
  display: grid;
  place-items: center;
  text-align: center;
  margin: 2rem auto;
}





/* ---------------- work section end -------------------*/
/* ---------------- about us section start-------------------*/
.common-section {
  margin: 5rem 0;
}

.our-services img {
  width: 40%;
  height: 75%;
  margin-top: 10rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.8rem 0.8rem;
  border-radius: 1.5rem;
}

.our-services .mini-title {
  text-transform: uppercase;
  font-weight: normal;
  margin: 3rem 0;
  font-size: 1.5rem;
  text-align: left;
}

.our-services .main-heading {
  font-weight: bolder;
  text-transform: capitalize;
}

.our-serives-info {
  margin-top: 4rem;
}

.our-services .our-serives-number {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: transparent;
  border: 0.1rem solid var(--primary-color);
  font-size: 1.5rem;
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.our-serives-data {
  padding-left: 2rem;
  text-transform: capitalize;
}

.our-services .our-serives-data p {
  margin: 1rem 0;
}

/* ---------------- about us section end-------------------*/
/* ---------------- contact page start-------------------*/
.container-section {
  margin: 15rem 0;
}

.contact-leftside img {
  min-width: 80%;
  max-height: 30rem;
  height: auto;
}

.contact-input-feild {
  margin-bottom: 4rem;
}

.contact-rightside form .form-control {
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  height: 5rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  padding-left: 1rem;
  font-weight: lighter;
}


.contact-rightside form .form-check-input {
  width: 2rem;
  height: 2rem;
}

.form-checkbox-style {
  margin: 4rem 0;
}

.contact-rightside form .main-hero-para {
  font-size: 1.5rem;
  color: var(--text-color);
  margin: 0;
  font-weight: lighter;
  width: 75%;
  padding: 3.7rem 2rem;
}

/* ---------------- contact page end-------------------*/
/* ---------------- services start-------------------*/

.service-main-container {
  margin-top: 5rem;
  padding: 5rem 0;
}

.service-main-container .main-heading {
  margin: 3rem 0;
}

.work-container-subdiv {
  box-shadow: 0 0.1rem 0.4rem rgba(0, 0, 0, 0.16);
  cursor: pointer;

}

.work-container-subdiv:hover {
  background-color: var(--bgcolor);
}

.service-main-container .work-container-subdiv .fontawesome-style {
  text-align: left;
  margin: 2rem 0;
}

.service-main-container .work-container-subdiv:hover>.fontawesome-style {
  background-color: #fff;
}

.work-container-subdiv .main-hero-para {
  width: 100%;
}


@media (min-width: 1400px) {
  .service-container .col-xxl-4 {
    flex: 0 0 auto;
    width: 30% !important;
    margin: auto;
  }
}

@media (min-width: 992px) {
  .service-container .col-xxl-4 {
    flex: 0 0 auto;
    width: 30% !important;
    margin: 2.5rem auto;
  }
}

/* ----------------services end-------------------*/
/* ----------------Footer start-------------------*/
footer {
  background-color: #182334;
  color: #fff;
  padding: 5rem 0;
}

footer h2 {
  color: #fff;
}

footer li {
  list-style: none;
  color: #fff;
  cursor: pointer;
  font-size: 1.5rem;
  margin: 1rem 0;
  font-weight: lighter;
}

footer .main-hero-para {
  margin: 0;
}

/* ----------------Footer end-------------------*/



